import React from 'react';
import Card from '../Card/Card';
import s from "./Works.module.css";
import img1 from "../../assets/img/1.png";
import img2 from "../../assets/img/2.png";
import img3 from "../../assets/img/3.png";
import { NavLink } from 'react-router-dom';
import { Carousel } from '@trendyol-js/react-carousel';
import { ArrowLeft, ArrowRigth } from './Arrow';
const Works = (props) => {
    let x = <span>Были разработаны <span className="skills_bold">UI-kit: система компонентов и стилей.</span></span>
    return (

        <div className={s.container}>
            <div className={s.content}>
                <Carousel show={4.5} swiping={true} >
                    <Card
                        title='Soft-managment'
                        decription={["В разработке"]}
                        skills={["Miro",]}
                    />
                    <NavLink to="Banner">
                        <Card
                            title='Баннеры'
                            decription={["Баннеры для постов, промо-блоков и т.д.", "Используемый метод разработки — коллажирование."]}
                            skills={["Ps"]}
                            img={img1}
                        />
                    </NavLink>
                    <NavLink to="LostShirt"> <Card
                        title='Lost Shirt'
                        decription={["Вёрстка и frontend сайта принтов для одежды."]}
                        skills={["Html", "Css", "Js", "GitHub"]}
                        img={img2}
                    /></NavLink>
                    <NavLink to="Anion">
                        <Card
                            title='Анион'
                            decription={["Редизайн сайта компании «Анион»",]}
                            skills={["Figma", "Ps",]}
                            img={img3}
                        />
                    </NavLink>
                    <Card
                        title='Клинкманн'
                        decription={["В разработке",]}
                        skills={["Axure",]}

                    />
                    <Card
                        title='Let’s Rock'
                        decription={["В разработке"]}
                        skills={["Figma", "Ps"]}

                    />

                </Carousel>
            </div>
        </div>
    );
};

export default Works;